@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
.full {
  font-family: "Montserrat", sans-serif;
}
.home {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  margin: auto;
}

.leftSide__home {
  font-size: 25px;
}

.leftSide__home h2 {
  line-height: 20px;
  padding-right: 25px;
}

span {
  color: rgb(222, 125, 241);
}
.home__button {
  font-size: 21px;
  padding: 10px 20px;
  color: white;
  background-color: rgb(222, 125, 241);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: 0.5s ease-in-out;
  margin-left: 160px;
}

.home__button:hover {
  background: transparent;
  color: rgb(222, 125, 241);
  border: 1px solid rgb(222, 125, 241);
}
img {
  width: 50%;
  padding: 35px;
  margin-left: 100px;
}
.downBTN {
  margin-right: 100px;
  margin: 25px;
  margin-bottom: 120px;
}

.homesection__review {
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .home {
    flex-wrap: wrap;
    text-align: center;
  }
  .home img {
    margin-top: 30px;
  }
}
